import { Link, useLocation } from "react-router-dom";

/**
 * Composant qui donne accs  la gestion des utilisateurs et des btiments
 * @returns 
 */
const Admin = () => {

    const location = useLocation();

    let user;

    if (location.state != null) {
        user = location.state.u;
    }

    if (user) {
        return (
            <div className="adminPage">
                <h2>Espace administration</h2>
                <p>Vous tes dans l'espace de gestion des btiments, des photos associes et des utilisateurs.</p>
                <Link className="btn btn-primary" to="/admin/buildings">Voir les btiments</Link>
                <Link className="btn btn-primary mx-1" to="/admin/users">Voir les utilisateurs</Link>
            </div>
        )
    } else {
        return (
            <div>
                <p>Vous devez tre connect pour accder  l'espace administration.</p>
                <Link to="/">Retour  la page d'acceuil</Link>
            </div>
        );
    }
}

export default Admin;